@import "monokai";

//$back: black;
$back: #272822;
$text: #fff;
$mainFont: 'Poppins', sans-serif;
$titleFont: 'Titillium Web', sans-serif;


/*
*
CSS for default.html
*
*/
* {
	margin: 0px;
    padding: 0px;
    background: $back;
    color: $text;
	box-sizing: border-box;
}

body
{
    //min-width:330px;
	font-family: $mainFont;
}


/*
*
CSS for logo, navbar.html
*
*/


header
{
	display: flex;
	align-items: center;
}

.logo-container, .nav-links,
{
	display: flex;
}

.logo-container
{
	flex: 1;
    
    .logo
    { 
        max-width: 100%;
        max-height: 85%;
	    width: 120px;
        height: 80px;
	    //margin: auto;
    }

    h4.title
    {
        display: flex;
        flex: 1;
        font-size: 24px;
        font-family: $titleFont;
        align-self: center;
    }

    a.url
    {
        display: flex;
        flex: 1;
        text-decoration: none;
    }
}
    
